:'Comfortaa', cursive;color:#ff3f34;font-weight:bold;margin-left:10px}.bar{width:100%;height:7px;background-color:#808e9b;border-radius:5px}.item{display:flex;flex-direction:column;justify-content:space-evenly;align-items:center}.ii{height:100%;background-color:#485460;border-radius:5px;display:flex;justify-content:start;align-items:center}.logo-img{padding:20px;width:50px;height:50  px}.logo-img .icon{outline-width:10px;color:#485460;font-size:50px}@media only screen and (max-width: 900px){.info-container{grid-gap:5px 5px !important}.info-container .info-container{color:#ff5e57;font-size:10px}.info-container .info-container .info-span{margin-right:5px;color:#1e272e;text-align:start}.skills-container{flex-direction:column}.skills-container .skills-titile{font-family:'Raleway', sans-serif;transform:rotate(0);font-size:30px}.skills-container .skills{justify-content:center;margin-top:20px;display:grid;grid-gap:10px !important;grid-template-columns:repeat(1, 300px);text-align:center}.skills-container .skill-title{margin-top:-20px;display:flex;justify-content:space-between;flex-direction:row;align-items:center;text-align:center}.skills-container .skill-title .skill-name{font-size:15px}.bar{height:5px}.logo-img{padding:10px;width:50px;height:50px}.logo-img .icon{outline-width:10px;color:#485460;font-size:30px}.image-desc{justify-content:center;width:100% !important}.image-desc .desc{align-items:center !important;width:100% !important}.image-desc .desc .image-3{display:block !important;width:20%;height:auto;border-radius:50%}.image-desc .desc .image-3 img{width:20vw;height:20vw;object-fit:cover;border-radius:50%}.image-desc .desc .about-titile{font-size:30px;text-align:center}.image-2{display:none !important}.paraga{font-size:10px}}

